<template>
  <div>
    <!-- 酒店审核 -->
    <div id="hotel-info">
      <div class="info-title m10">{{ $route.query.hotelName }}</div>
      <div class="info-addr m10">{{ $route.query.address }}</div>
      <div class="info-name m10">{{ $route.query.contacts }}&emsp;{{ $route.query.phone }}</div>
      <div class="info-pic m10">
        <div class="pic-box"
             @click="check($route.query.domainName+$route.query.frontDoorImage)">
          <img :src="$route.query.domainName+$route.query.frontDoorImage"
               alt=""
               class="pic">
          <div>门头照片</div>
        </div>
        <div class="pic-box"
             @click="check($route.query.domainName+$route.query.roomImage)">
          <img :src="$route.query.domainName+$route.query.roomImage"
               alt=""
               class="pic">
          <div>房间照片</div>
        </div>
        <div class="pic-box"
             @click="check($route.query.domainName+$route.query.lockImage)">
          <img :src="$route.query.domainName+$route.query.lockImage"
               alt=""
               class="pic">
          <div>门锁照片</div>
        </div>
        <div class="pic-box"
             @click="check($route.query.domainName+$route.query.licenseImage)">
          <img :src="$route.query.domainName+$route.query.licenseImage"
               alt=""
               class="pic">
          <div>营业执照</div>
        </div>
      </div>
      <van-button class="apply-ok m10"
                  @click="agree">通过申请</van-button>
      <van-button class="apply-no"
                  @click="refuse_show = true">拒绝申请</van-button>
      <!-- 拒绝框 -->
      <van-dialog use-slot
                  title='拒绝申请'
                  v-model='refuse_show'
                  show-cancel-button
                  confirm-button-open-type="getUserInfo"
                  @close="refuse_show = false"
                  @confirm="refuse">
        <!-- <div class="refuse-hotel">{{ $route.query.hotelName }}</div> -->
        <van-field class="refuse-input"
                   v-model="auditFailReason"
                   type="textarea"
                   placeholder="请输入拒绝原因"
                   autosize />
      </van-dialog>
      <!-- 查看原图 -->
      <van-popup v-model='photo_show'
                 style="width:90%;height:40%;">
        <img :src="current_pic"
             class="pig-pic"
             alt="">
      </van-popup>
    </div>
  </div>
</template>
<script>
import { PostBusinessHotelJoinApplyAgree, PostBusinessHotelJoinApplyRefuse } from '@/api/api_join';
import { Toast, Dialog } from 'vant';
export default {
    data() {
        return {
            photo_show: false,
            refuse_show: false,
            auditFailReason: '',
            current_pic: null
        };
    },
    created() {
        this.$store.commit('setHeader', '酒店审核');
    },
    methods: {
        // 查看原图
        check(pic) {
            this.current_pic = pic;
            this.photo_show = true;
        },
        // 酒店加盟审批通过
        agree() {
            Dialog.confirm({
                title: '通过申请?',
                message: this.$route.query.hotelName
            })
                .then(() => {
                    PostBusinessHotelJoinApplyAgree({ hotelJoinApplyId: this.$route.query.id }).done((res) => {
                        Toast.success(res.msg);
                        setTimeout(() => {
                            this.$router.go(-1);
                        }, 2000);
                    });
                })
                .catch(() => {});
        },
        // 酒店加盟审批拒绝
        refuse() {
            let data = {
                auditFailReason: this.auditFailReason,
                hotelJoinApplyId: this.$route.query.id
            };
            PostBusinessHotelJoinApplyRefuse(data).done((res) => {
                Toast.success(res.msg);
                setTimeout(() => {
                    this.$router.go(-1);
                }, 2000);
            });
        }
    }
};
</script>
<style scoped>
#hotel-info {
    color: #000;
    margin: 30px 10px;
    text-align: left;
    font-size: 15px;
}
.info-title {
    font-size: 1.2rem;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}
.info-addr {
    font-size: 12px;
    color: #888;
    line-height: 14px;
}
.info-name {
    line-height: 40px;
    background: #efeff4;
    border-radius: 4px;
    padding-left: 10px;
}
.info-pic {
    display: flex;
    justify-content: space-between;
    color: #989898;
    font-size: 12px;
}
.pic-box {
    text-align: center;
}
.pic {
    height: 60px;
    width: 60px;
    border-radius: 4px;
}
.pig-pic {
    width: 100%;
    position: absolute;
    left: 0;
    right: 0;
    height: 100%;
    top: 0;
    bottom: 0;
}
.refuse-input {
    margin: 15px;
    background: #efeff4;
    border-radius: 5px;
    height: 3rem;
    width: 90%;
    height: 100px;
    overflow-y: auto;
}
.refuse-hotel {
    margin: 10px auto;
    text-align: center;
    font-size: 14px;
}
.apply-ok {
    width: 100%;
    background: #ff7d41;
    color: #fff;
    border-radius: 4px;
}
.apply-no {
    width: 100%;
    background: #efeff4;
    color: #000;
    border-radius: 4px;
}
.m10 {
    margin: 20px 0;
}
</style>



